.tm-page {
  border-top-width: 0;
}
@media (min-width: 1200px) {
  .tm-page {
    border-width: 50px;
  }
}

@media (min-width: 960px) {
  .tm-page {
    border: 30px solid transparent;
  }
}
@media (min-width: 960px) {
  #margin-top-140 {
    margin-top: 140px;
  }
}
@media (min-width: 640px) {
  #padding-top-10vw > div {
    padding-top: 10vw !important;
  }
}
@media (min-width: 960px) {
  #margin-top-80 .uk-first-column > .uk-panel {
    margin-top: 80px;
  }
}
@media (min-width: 960px) {
  #padding-top-280 > div {
    padding-top: 280px;
  }
}
.uk-slidenav-container{
  background: #fff;

}
.uk-slidenav{
  background: #fff;
  padding: 21px 18px;

}
.tm-page {
  position: relative;
  z-index: 0;
}
.uk-margin {
  margin-bottom: 30px;
}
